@import "normalize.css";

@font-face{
  font-family: 'Astakhov';
  src: url(../fonts/Astakhov-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  font-family: Astakhov;
}

body, ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
}

button, input {
  padding: 0;
  border: 0px;
  cursor: pointer;

  &:active, &:focus {
    outline: none;
  }
}

li {
  list-style: none;
}

.main {
  margin: 0 auto;
  max-width: 1920px;
}

.top {
  height: 70vh;
}

.bottom-bar
{
  padding: 0 5%;

  &-container {
    display: flex;
    background: url(../img/bottom-bar-body.png) no-repeat;
    background-size: contain;
    width: 100%;
    height: 7.5vw;
  }

  &__info {
    background: url(../img/info-normal-bg.png) no-repeat;
    background-size: contain;
    min-width: 4.34%;
    max-width: 4.34%;
    max-height: 52%;
    margin-left: 5.3%;
    margin-top: 2.5%;

    &-icon {
      background: url(../img/info-normal-icon.png) no-repeat;
      background-size: contain;
      margin: 0 auto;
      height: 58%;
      max-width: 20%;
    }

    &:hover {
      background: url(../img/info-hover-bg.png) no-repeat;
      background-size: contain;
    }

    &:active {
      background: url(../img/info-click-bg.png) no-repeat;
      background-size: contain;
    }

    &:disabled {
      background: url(../img/info-disable-bg.png) no-repeat;
      background-size: contain;
    }

    &:disabled &-icon {
      background: url(../img/info-disable-icon.png) no-repeat;
      background-size: contain;
    }
  }

  &__bet {
    background: url(../img/bet-bg.png) no-repeat;
    background-size: contain;
    display: flex;
    min-width: 15.45%;
    max-width: 15.45%;
    max-height: 52%;
    margin-top: 2.5%;
    margin-left: 0.5%;

    &-labels {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
    }

    &-minus {
      background: url(../img/bet-btn-normal-bg.png) no-repeat;
      background-size: contain;
      width: 22.5%;
      height: 81%;
      margin-top: 2.5%;
      margin-left: 3%;

      &-icon {
        background: url(../img/minus-icon-normal.png) no-repeat;
        background-size: contain;
        background-position: center;
        width: 48%;
        height: 48%;
        margin: 0 auto;
      }
    }

    &-plus {
      background: url(../img/bet-btn-normal-bg.png) no-repeat;
      background-size: contain;
      width: 22.5%;
      height: 81%;
      margin-top: 2.5%;
        margin-right: 3%;

      &-icon {
        background: url(../img/plus-icon-normal.png) no-repeat;
        background-size: contain;
        width: 48%;
        height: 48%;
        margin: 0 auto;
      }
    }

    &-plus:hover, &-minus:hover {
      background: url(../img/bet-btn-hover-bg.png) no-repeat;
      background-size: contain;
    }

    &-plus:active, &-minus:active {
      background: url(../img/bet-btn-click-bg.png) no-repeat;
      background-size: contain;
    }

    &-plus:disabled, &-minus:disabled {
      background: url(../img/bet-btn-disable-bg.png) no-repeat;
      background-size: contain;
    }

    &-plus:disabled .bottom-bar__bet-plus-icon {
      background: url(../img/plus-icon-disable.png) no-repeat;
      background-size: contain;
    }

    &-minus:disabled .bottom-bar__bet-minus-icon {
      background: url(../img/minus-icon-disable.png) no-repeat;
      background-size: contain;
    }
  }

  &__coin {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: url(../img/coin-bg.png) no-repeat;
    background-size: contain;
    min-width: 8%;
    max-width: 8%;
    max-height: 49%;
    margin-top: 2.5%;
    margin-left: 0.5%;
  }
}

.bottom-bar__bet-head, .bottom-bar__coin-head {
  color: #ff6972;
  text-shadow: 0px 1px 0px #4e0005;
  font-size: calc( (100vw - 320px)/(1920 - 320) * (18 - 3) + 3px);
}

.bottom-bar__bet-count, .bottom-bar__coin-count {
  color: #ffd100;
  text-shadow: 0px 1px 0px #710008;
  font-size: calc( (100vw - 320px)/(1920 - 320) * (22 - 4) + 4px);
}